import { $, $$, constants } from './__constants';

/* eslint-env browser */
function sampleRUM(checkpoint?, data?) {
  const timeShift = () =>
    window.performance
      ? window.performance.now()
      : Date.now() - (window as any).hlx.rum.firstReadTime;
  try {
    (window as any).hlx = (window as any).hlx || {};
    if (!(window as any).hlx.rum) {
      (sampleRUM as any).enhance = () => {};
      const param = new URLSearchParams(window.location.search).get('rum');
      const weight =
        (param === 'on' && 1) ||
        ((window as any).SAMPLE_PAGEVIEWS_AT_RATE === 'high' && 10) ||
        ((window as any).SAMPLE_PAGEVIEWS_AT_RATE === 'low' && 1000) ||
        100;
      const id = Math.random().toString(36).slice(-4);
      const isSelected = param !== 'off' && Math.random() * weight < 1;
      (window as any).hlx.rum = {
        weight,
        id,
        isSelected,
        firstReadTime: window.performance ? window.performance.timeOrigin : Date.now(),
        sampleRUM,
        queue: [],
        collector: (...args) => (window as any).hlx.rum.queue.push(args),
      };
      if (isSelected) {
        const dataFromErrorObj = (error) => {
          const errData = { source: 'undefined error', target: '' };
          try {
            errData.target = error.toString();
            errData.source = error.stack
              .split('\n')
              .filter((line) => line.match(/https?:\/\//))
              .shift()
              .replace(/at ([^ ]+) \((.+)\)/, '$1@$2')
              .replace(/ at /, '@')
              .trim();
          } catch (err) {
            console.error(err);
            /* error structure was not as expected */
          }
          return errData;
        };

        window.addEventListener('error', ({ error }) => {
          const errData = dataFromErrorObj(error);
          sampleRUM('error', errData);
        });

        window.addEventListener('unhandledrejection', ({ reason }) => {
          let errData = {
            source: 'Unhandled Rejection',
            target: reason || 'Unknown',
          };
          if (reason instanceof Error) {
            errData = dataFromErrorObj(reason);
          }
          sampleRUM('error', errData);
        });

        (sampleRUM as any).baseURL =
          (sampleRUM as any).baseURL ||
          new URL((window as any).RUM_BASE || '/', new URL('https://rum.hlx.page'));
        (sampleRUM as any).collectBaseURL =
          (sampleRUM as any).collectBaseURL || (sampleRUM as any).baseURL;
        (sampleRUM as any).sendPing = (ck, time, pingData = {}) => {
          const rumData = JSON.stringify({
            weight,
            id,
            referer: window.location.href,
            checkpoint: ck,
            t: time,
            ...pingData,
          });
          const urlParams = (window as any).RUM_PARAMS
            ? `?${new URLSearchParams((window as any).RUM_PARAMS).toString()}`
            : '';
          const { href: url, origin } = new URL(
            `.rum/${weight}${urlParams}`,
            (sampleRUM as any).collectBaseURL,
          );
          const body =
            origin === window.location.origin
              ? new Blob([rumData], { type: 'application/json' })
              : rumData;
          navigator.sendBeacon(url, body);
          console.debug(`ping:${ck}`, pingData);
        };
        (sampleRUM as any).sendPing('top', timeShift());

        (sampleRUM as any).enhance = () => {
          // only enhance once
          if ($(document, 'script[src*="rum-enhancer"]')) {
            return;
          }
          const { enhancerVersion, enhancerHash } = (sampleRUM as any).enhancerContext || {};
          const script = document.createElement('script');
          if (enhancerHash) {
            script.integrity = enhancerHash;
            script.setAttribute('crossorigin', 'anonymous');
          }
          script.src = new URL(
            `.rum/@adobe/helix-rum-enhancer@${enhancerVersion || '^2'}/src/index.js`,
            (sampleRUM as any).baseURL,
          ).href;
          document.head.appendChild(script);
        };
        if (!(window as any).hlx.RUM_MANUAL_ENHANCE) {
          (sampleRUM as any).enhance();
        }
      }
    }
    if ((window as any).hlx.rum && (window as any).hlx.rum.isSelected && checkpoint) {
      (window as any).hlx.rum.collector(checkpoint, data, timeShift());
    }
    document.dispatchEvent(new CustomEvent('rum', { detail: { checkpoint, data } }));
  } catch (err) {
    console.error(err);
    // something went awry
  }
}

/**
 * Setup block utils.
 */
function setup() {
  (window as any).hlx = (window as any).hlx || {};
  (window as any).hlx.RUM_MASK_URL = 'full';
  (window as any).hlx.RUM_MANUAL_ENHANCE = true;
  (window as any).hlx.codeBasePath = '';
  (window as any).hlx.lighthouse =
    new URLSearchParams(window.location.search).get('lighthouse') === 'on';

  const scriptEl = $(document, 'script[src$="/scripts/scripts.js"]');
  if (scriptEl) {
    try {
      [(window as any).hlx.codeBasePath] = new URL((scriptEl as any).src).pathname.split(
        '/scripts/scripts.js',
      );
    } catch (error) {
      console.log(error);
    }
  }
}

/**
 * Auto initialization.
 */

function init() {
  setup();
  (sampleRUM as any).collectBaseURL = window.origin;
  sampleRUM();
}

/**
 * Sanitizes a string for use as class name.
 * @param {string} name The unsanitized string
 * @returns {string} The class name
 */
function toClassName(name) {
  return typeof name === 'string'
    ? name
        .toLowerCase()
        .replace(/[^0-9a-z]/gi, '-')
        .replace(/-+/g, '-')
        .replace(/^-|-$/g, '')
    : '';
}

/**
 * Sanitizes a string for use as a js property name.
 * @param {string} name The unsanitized string
 * @returns {string} The camelCased name
 */
function toCamelCase(name) {
  return toClassName(name).replace(/-([a-z])/g, (g) => g[1].toUpperCase());
}

/**
 * Extracts the config from a block.
 * @param {Element} block The block element
 * @returns {object} The block config
 */
function readBlockConfig(block) {
  const config = {};
  block.querySelectorAll(':scope > div').forEach((row) => {
    if (row.children) {
      const cols = [...row.children];
      if (cols[1]) {
        const col = cols[1];
        const name = toClassName(cols[0].textContent);
        let value: string | any[] = '';
        if (col.querySelector('a')) {
          const as = [...col.querySelectorAll('a')];
          if (as.length === 1) {
            value = as[0].href;
          } else {
            value = as.map((a) => a.href);
          }
        } else if (col.querySelector('img')) {
          const imgs = [...col.querySelectorAll('img')];
          if (imgs.length === 1) {
            value = imgs[0].src;
          } else {
            value = imgs.map((img) => img.src);
          }
        } else if (col.querySelector('p')) {
          const ps = [...col.querySelectorAll('p')];
          if (ps.length === 1) {
            value = ps[0].textContent;
          } else {
            value = ps.map((p) => p.textContent);
          }
        } else {
          value = row.children[1].textContent;
        }
        config[name] = value;
      }
    }
  });
  return config;
}

/**
 * Loads a CSS file.
 * @param {string} href URL to the CSS file
 */
async function loadCSS(href) {
  return new Promise((resolve, reject) => {
    if (!$(document, `head > link[href="${href}"]`)) {
      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = href;
      link.onload = resolve;
      link.onerror = reject;
      document.head.append(link);
    } else {
      resolve('');
    }
  });
}

/**
 * Loads a non module JS file.
 * @param {string} src URL to the JS file
 * @param {Object} attrs additional optional attributes
 */
async function loadScript(src, attrs) {
  return new Promise((resolve, reject) => {
    if (!$(document, `head > script[src="${src}"]`)) {
      const script = document.createElement('script');
      script.src = src;
      if (attrs) {
        for (const attr in attrs) {
          script.setAttribute(attr, attrs[attr]);
        }
      }
      script.onload = resolve;
      script.onerror = reject;
      document.head.append(script);
    } else {
      resolve('');
    }
  });
}

/**
 * Retrieves the content of metadata tags.
 * @param {string} name The metadata name (or property)
 * @param {Document} doc Document object to query for metadata. Defaults to the window's document
 * @returns {string} The metadata value(s)
 */
function getMetadata(name, doc = document) {
  const attr = name && name.includes(':') ? 'property' : 'name';
  const meta = [...doc.head.querySelectorAll(`meta[${attr}="${name}"]`)]
    .map((m) => (m as HTMLMetaElement).content)
    .join(', ');
  return meta || '';
}

/**
 * Gets all the metadata elements that are in the given scope.
 * @param {String} scope The scope/prefix for the metadata
 * @returns an array of HTMLElement nodes that match the given scope
 */
function getAllMetadata(scope) {
  return [
    ...document.head.querySelectorAll(`meta[property^="${scope}:"],meta[name^="${scope}-"]`),
  ].reduce((res, meta) => {
    const id = toClassName(
      (meta as HTMLMetaElement).name
        ? (meta as HTMLMetaElement).name.substring(scope.length + 1)
        : meta.getAttribute('property').split(':')[1],
    );
    res[id] = meta.getAttribute('content');
    return res;
  }, {});
}

/**
 * Returns a picture element with webp and fallbacks
 * @param {string} src The image URL
 * @param {string} [alt] The image alternative text
 * @param {boolean} [eager] Set loading attribute to eager
 * @param {Array} [breakpoints] Breakpoints and corresponding params (eg. width)
 * @returns {Element} The picture element
 */
function createOptimizedPicture(
  src,
  alt = '',
  eager = false,
  breakpoints = [{ media: '(min-width: 600px)', width: '2000' }, { width: '750' }],
) {
  const url = new URL(src, window.location.href);
  const picture = document.createElement('picture');
  const { pathname } = url;
  const ext = pathname.substring(pathname.lastIndexOf('.') + 1);

  // webp
  breakpoints.forEach((br) => {
    const source = document.createElement('source');
    if (br.media) {
      source.setAttribute('media', br.media);
    }
    source.setAttribute('type', 'image/webp');
    source.setAttribute('srcset', `${pathname}?width=${br.width}&format=webply&optimize=medium`);
    picture.appendChild(source);
  });

  // fallback
  breakpoints.forEach((br, i) => {
    if (i < breakpoints.length - 1) {
      const source = document.createElement('source');
      if (br.media) {
        source.setAttribute('media', br.media);
      }
      source.setAttribute('srcset', `${pathname}?width=${br.width}&format=${ext}&optimize=medium`);
      picture.appendChild(source);
    } else {
      const img = document.createElement('img');
      img.setAttribute('loading', eager ? 'eager' : 'lazy');
      img.setAttribute('alt', alt);
      picture.appendChild(img);
      img.setAttribute('src', `${pathname}?width=${br.width}&format=${ext}&optimize=medium`);
    }
  });

  return picture;
}

/**
 * Set template (page structure) and theme (page styles).
 */
function decorateTemplateAndTheme() {
  const addClasses = (element, classes) => {
    classes.split(',').forEach((c) => {
      element.classList.add(toClassName(c.trim()));
    });
  };
  const template = getMetadata('template');
  if (template) {
    addClasses(document.body, template);
  }
  const theme = getMetadata('theme');
  if (theme) {
    addClasses(document.body, theme);
  }
}

/**
 * Wrap inline text content of block cells within a <p> tag.
 * @param {Element} block the block element
 */
function wrapTextNodes(block: HTMLElement) {
  const validWrappers = [
    'P',
    'PRE',
    'UL',
    'OL',
    'PICTURE',
    'TABLE',
    'H1',
    'H2',
    'H3',
    'H4',
    'H5',
    'H6',
  ];
  const wrap = (el: HTMLElement) => {
    const wrapper = document.createElement('p');
    wrapper.append(...el.childNodes);
    el.append(wrapper);
  };
  block.querySelectorAll(':scope > div > div').forEach((blockColumn) => {
    if (blockColumn.hasChildNodes()) {
      const hasWrapper =
        !!blockColumn.firstElementChild &&
        validWrappers.some((tagName) => blockColumn.firstElementChild.tagName === tagName);
      if (!hasWrapper) {
        wrap(blockColumn as HTMLElement);
      } else if (
        blockColumn.firstElementChild.tagName === 'PICTURE' &&
        (blockColumn.children.length > 1 || !!blockColumn.textContent.trim())
      ) {
        wrap(blockColumn as HTMLElement);
      }
    }
  });
}

